html, body {
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  width: 100%;
  height: 100%;
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  min-height: 100vh;
  background-color: #f5f5f5;
  color: #313131;
  letter-spacing: -.022em;
  font-weight: 400;
  font-style: normal;
  font-size: 17px;
  font-family: Verdana, Tahoma, Geneva, sans-serif;
  line-height: 1.47059;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
}

.wrapper {
  display: flex;
  overflow: hidden;
  flex: 1 1 auto;
  flex-direction: column;
  min-height: 100%;
  width: 100%;
}

.content {
  flex: 1 0 auto;
  margin: 70px 0 0 0;
}

.header, .footer {
  flex: 0 0 auto;
  background: #49525c;
  color: #e6e6e6;
  font-size: 12px;
}

.img-wrap {
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  height: auto;
}

.img-wrap img, .img-wrap svg, .img-wrap picture {
  max-width: 100%;
  transition: all .2s linear;
  object-fit: cover;
}

