/*! footer */
.footer {
  padding: 20px 0;
  line-height: 1.7;
}

.footer__logo {
  display: block;
}

.footer__link {
  bottom: 0;
  display: block;
  margin: 0 5px;
  font-size: 13px;
  position: relative;
  color: #ff6363;
  font-weight: 700;
}

.footer__link:before {
  position: absolute;
  bottom: 1px;
  left: 50%;
  display: flex;
  width: 0;
  height: 1px;
  background: #ff6363;
  content: "";
  transition: all .2s ease-in-out;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

.footer__link:hover:before {
  width: 100%;
}

.footer__link:hover {
  color: #ff6363;
  transition: all .2s ease-in-out;
}

.contacts__block {
  display: flex;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.contacts__block div {
  display: flex;
}

.socials__block {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
}

.socials__text {
  margin: 0 0 0 5px;
}

.socials__link {
  display: block;
  margin: 0 0 0 6px;
  width: 29px;
  height: 28px;
  position: relative;
  border-radius: 2px;
  transition: all .2s ease-in-out;
}

.socials__link::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 2px;
  box-shadow: 0 0 1px 1px #ff6363;
  content: "";
  opacity: 0;
  transition: all .2s ease-in-out;
}

.socials__link:hover::before {
  opacity: 1;
  transition: all .2s ease-in-out;
  -webkit-transform: scale(1.15);
  transform: scale(1.15);
}

.socials__link svg path:first-child {
  transition: all .2s ease-in-out;
}

.socials__link:hover {
  transition: all .2s ease-in-out;
}

.socials__link:hover svg path:first-child {
  transition: all .2s ease-in-out;
  fill: #ff6363;
}

@media (max-width: 1199.98px) {
  .socials__text {
    margin: 0 0 5px 5px;
  }
}

@media (max-width: 767.98px) {
  .footer__logo {
    margin: 0 0 15px 0;
  }
  .contacts {
    margin: 0 0 15px 0;
  }
  .socials__text {
    margin: 0 0 5px 0;
  }
  .socials__link {
    margin: 0 3px;
  }
}

@media (max-width: 575.98px) {
  .contacts__block {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

